.App {
  /* text-align: center; */
  background-color: #282c34;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.row:after {
  content: "";
  display: table;
  clear: both;
}

.column{
  text-align: center;
  float: left;
  width: 33.33%;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-header {
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
